import React from 'react'
import './Button.css'

const isOperator = val => {
    return !isNaN(val) || val === '.' || val === '=';
}

export default function Button({addToInput, children}) {
    return <div onClick={() => addToInput(children)} className={`number ${isOperator(children) ? null : 'operator'}`}>{children}</div>
}
