import React, { useState } from 'react'
import Button from '../Buttons/Button'
import ClearButton from '../Buttons/ClearButton'
import Input from '../Input/Input'
import './App.css'

export default function App() {

    const [input, setInput] = useState('0')
    const [memory, setMemory] = useState('')
    const [operator, setOperator] = useState('')

    const num = parseFloat(input)

    function addToInput(val) {
        if(input[input.length -1] === '.') {
            setInput(input + val)
        } else {
            setInput(parseFloat(num + val).toString())
        }


    }

    function clear() {
        setInput('0')
        setMemory(null)
        setOperator(null)
    }
    
    function dicimal(val) {
        if(input[input.length -1] === '.') {
            setInput(input + val)
        } else {
            setInput(parseFloat(num + val).toString())
        }

        if (input.includes('.')) return
        setInput(input + '.')
    }

    function plus() {
        setMemory(parseFloat(input))
        setInput('0')
        setOperator('+')
    }
    function minus() {
        setMemory(parseFloat(input))
        setInput('0')
        setOperator('-')
    }
    function multiply() {
        setMemory(parseFloat(input))
        setInput('0')
        setOperator('*')
    }
    function divis() {
        setMemory(parseFloat(input))
        setInput('0')
        setOperator('/')
    }
    function equal() {
        if (!operator) return;
        if (operator === '+') {
            setInput(memory + parseFloat(input) )
        } else if (operator === '-') {
            setInput(memory - parseFloat(input) )
        } else if (operator === '*') {
            setInput(memory * parseFloat(input) )
        } else if (operator === '/') {
            setInput(memory / parseFloat(input) )
        }
        setMemory(null)
        setOperator(null)
    }

    return (
        <div className='app'>
            <div>
            <Input input={input}/>
                <div className='numbers'>
                    <Button addToInput={addToInput}>7</Button>
                    <Button addToInput={addToInput}>8</Button>
                    <Button addToInput={addToInput}>9</Button>
                    <Button addToInput={multiply}>*</Button>
                </div>
                <div className='numbers'>
                    <Button addToInput={addToInput}>4</Button>
                    <Button addToInput={addToInput}>5</Button>
                    <Button addToInput={addToInput}>6</Button>
                    <Button addToInput={minus}>-</Button>
                </div>
                <div className='numbers'>
                    <Button addToInput={addToInput}>1</Button>
                    <Button addToInput={addToInput}>2</Button>
                    <Button addToInput={addToInput}>3</Button>
                    <Button addToInput={plus}>+</Button>
                </div>
                <div className='numbers'>
                    <Button addToInput={addToInput}>0</Button>
                    <Button addToInput={dicimal}>.</Button>
                    <Button addToInput={() => equal()}>=</Button>
                    <Button addToInput={divis}>/</Button>
                </div>
                <div className='numbers'>
                    <ClearButton clear={clear}/>
                </div>
            </div>  
        </div>
    )
}
